import React from 'react'
import { Button } from '@material-ui/core'
import {auth , provider} from '../Config/firebase'
import { connect } from 'react-redux'
import { SetUser } from '../../Action'
import './index.css'

function index(props) {
  let {SetUser} = props;
  const signInGoogle = ()=>{
    auth.signInWithPopup(provider)
    .then(result=>{
      SetUser(result.user)
    })
    .catch(error=>{
      console.log(error.message)
    })
  }
  const signInGithub = ()=>{
    
  }
  return (
    <div className="login">
      <div className="logo">
      <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/51/Facebook_f_logo_%282019%29.svg/800px-Facebook_f_logo_%282019%29.svg.png"
          alt="facebook logo"
          className="logo"
        />
      </div>
      <div className="login__method">
        <Button color="primary" variant="contained" onClick={signInGoogle}>Google</Button>
        <Button color="primary" variant="contained" onClick={signInGithub}>Github</Button>
      </div>
    </div>
  )
}

const mapDispatchToProps = dispatch=>{
  return {
    SetUser: user=>dispatch(SetUser(user))
  }
}

export default connect(null,mapDispatchToProps)(index)
